}

.rectangleNLine {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-size: 20px;
  background-color: rgb(114, 114, 114);
  margin: 5%;
  position: relative;
  font-weight: 500;
  margin-bottom: 20px;
  /* Add some space below the rectangle */
}

.subRectangle {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-size: 20px;
  background-color: rgb(114, 114, 114);
  margin: 5%;
  position: relative;
  font-weight: 500;
  margin-top: 10px;
  /* Move it down a little */
  margin-left: 50px;
  /* Move it down a little */
}



/* The arrow at the bottom */
.subRectangle::before {
  left: -14px;
  top: 48%;
  width: 2px;
  height: 54px;
  content: "";
  position: absolute;
  background-color: rgb(114, 114, 114);
  transform: translateY(-50%);
}



