.PayButton {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 12px; /* Adjusted for better readability */
  padding: 12px 16px; /* Added padding for a better look */
  border-radius: 50px;
  background-color: rgba(88, 55, 50, 1);
  color: white;
  border: none;
  margin: 0 auto;
  cursor: pointer;
  display: block; /* Centering the button */
  text-align: center;
}
.DeclineButton {
  font-family: "Plus Jakarta Sans", sans-serif;
  z-index: 201;
  position: relative;
  font-weight: 500;
  font-style: normal;
  font-size: 15px;
  padding: 12px 24px; /* Add some padding for spacing */
  color: rgba(88, 55, 50, 1);
  background-color: transparent; /* No background */
  border: none; /* No border */
  margin: 0 auto; /* Center horizontally */
  cursor: pointer;
  display: block; /* Center the text horizontally */
  text-align: center; /* Center the text within the button */
}

.DeclineButton.active {
  position: relative;
  z-index: 201;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 20px;
  padding: 12px 24px; /* Add some padding for spacing */
  color: rgba(88, 55, 50, 1);
  background-color: transparent; /* No background */
  border: none; /* No border */
  margin: 0 auto; /* Center horizontally */
  cursor: pointer;
  display: block; /* Center the text horizontally */
  text-align: center; /* Center the text within the button */
