.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  position: relative;
  z-index: 201; /* Make sure the button is above the replica */

  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 70%; /* Adjusted for better readability */
  padding: 12px 24px; /* Added padding for a better look */
  border-radius: 50px;
  background-color: rgba(88, 55, 50, 1);
  color: white;
  border: none;
  margin: 0 auto;
  cursor: pointer;
  align-items: center;
  text-align: center;
  display: inline-flex;
}

.replica {
  height: 40px;
  width: 140px;
  border-radius: 30px;
  position: absolute;
  background-color: rgba(146, 146, 146, 0); /* Semi-transparent blue */
  border: none;
  transition: all 0.5s ease-in-out;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 98; /* Behind the button */
}

.replica.active {
  width: 200vw; /* Full screen */
  height: 200vh; /* Full screen */
  position: absolute;
  overflow-y: hidden;
  top: 30%;
  z-index: 200;
  border-radius: 0px;
  background-color: white;
  pointer-events: none; /* Allow interactions */
}
.bussinessName {
  position: absolute;
  width: 60vw;
  top: 50%;
  left: 50%;
  color: rgba(0, 0, 0, 0);
  transform: translate(-50%, -50%);
  z-index: 98; /* Behind the button */
  transition: all 0.5s ease-in-out;
  font-size: 3vw;
  text-align: center;
  pointer-events: none;
}

.bussinessName.active {
  top: -900%;
  position: absolute;
  color: rgb(0, 0, 0);
  width: 60vw;
  right: 50%;
  z-index: 201; /* Behind the button */
  font-size: 3vw;
  text-align: center;
  transition: all 0.5s ease-in-out;
}
.bussinessName h2 {
  overflow-wrap: break-word;
  color: inherit; /* Ensures that it inherits the color from the parent */
}
.bussinessQR {
  position: absolute;
  height: 570%;
  width: 480%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 98; /* Behind the button */
  transition: all 0.5s ease-in-out;
}

.bussinessQR.active {
  position: absolute;
  top: -321%;
  transform: translate(-50%, -80%);
  display: flex;
  align-items: center;
  flex-direction: column;


  transition: all 0.5s ease-in-out;
  z-index: 201; /* Behind the button */
}

.downloadQR{
  font-size: 115%;
  padding-top: 10px;
  background-color: transparent;
  color: transparent;
  width: 178.89px;
}

.downloadQR.active{
  background-color: #8f8f8f;
  color: white;
  transition: all 0.5s ease-in-out;
}

.trapezoid {
  z-index: 201;
  position: absolute;
  top: 50%;
  left: 50%;
  color: rgba(0, 0, 0, 0);
  transform: translate(-200%, -50%);
  width: 0;
  height: 80px;
  border-top: 70px solid transparent;
  border-left: 100px solid transparent;
  border-bottom: 70px solid transparent;
  transition: all 0.5s ease-in-out;
}
.trapezoid.active {
  position: absolute;
  top: 50%;
  left: 50%;
  color: rgba(0, 0, 0, 0);
  transform: translate(-200%, -160%);
  width: 0;
  height: 80px;
  border-top: 70px solid transparent;
  border-left: 100px solid #EE2E3B;
  border-bottom: 70px solid transparent;
  transition: all 0.5s ease-in-out;
}

.rightTriangle {
  z-index: 201;
  position: absolute;
  top: 50%;
  left: 50%;
  color: rgba(0, 0, 0, 0);
  transform: translate(-4%, 120%);
  width: 0;
  height: 0;
  border-left: 184px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 128px solid #ee2e3b;
  transition: all 0.5s ease-in-out;
}
.rightTriangle.active {
  position: absolute;
  top: 50%;
  left: 50%;
  color: rgba(0, 0, 0, 0);
  transform: translate(-4%, 10%);
  width: 0;
  height: 0;
  border-left: 184px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 128px solid #ee2e3b;
  transition: all 0.5s ease-in-out;
}
.price {
  position: absolute;
  top: 50%;
  left: 50%;
  color: rgba(0, 0, 0, 0);
  transform: translate(-50%, -50%);
  z-index: 98; /* Behind the button */
  transition: all 0.5s ease-in-out;
  font-size: 3vw;
  text-align: center;
}

.price.active {
  position: absolute;
  color: rgb(0, 0, 0);
  transform: translate(-50%, -140%);
  z-index: 201; /* Behind the button */
  font-size: 3vw;
  text-align: center;
}

.ClaimButton {
  visibility: hidden;
  position: absolute;
}

.ClaimButton.active {
  visibility: visible;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 70%; /* Adjusted for better readability */
  padding: 12px 24px; /* Added padding for a better look */
  border-radius: 50px;
  background-color: rgba(88, 55, 50, 1);
  color: white;
  border: none;
  margin: 0 auto;
  cursor: pointer;
  display: block; /* Centering the button */
  text-align: center;
  z-index: 201;
}
