.itemContainer {
  display: flex;
  flex-direction: column;
  /* gap: 10px; */
}

.item {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  padding-left: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
  color: rgba(88, 55, 50, 1);
  font-size: 32px;
  box-sizing: border-box; /* Include padding and border in the element's total width */
  width: 100%; /* Ensure the item does not exceed the parent's width */
  overflow: hidden; /* Prevent internal overflow */
  padding-top: 10px;
  margin-bottom: 5px;
}

.item:not(.itemInvoice) {
  /* border-top: 2px solid #00000017; */
}

.notLast{
  padding-bottom: 10px;
  border-bottom: 2px solid #00000017;
}

.itemInvoice {
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  font-size: 18px;
  margin-top: 0px;
  margin-bottom: 0px;
  padding-top: 0px;
}

.itemInvoice:last-child {
  margin-bottom: 0; /* Remove margin-bottom for the last child */
}

.itemImage {
  width: 100%;
  height: 100%;
}

.imageContainer {
  position: relative;
  width: 26vw;
  height: 26vw;
  border-radius: 12px;
}

.overlay {
  position: absolute;
  top: 15px;
  left: 8px;
  right: 8px;
  bottom: 15px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 3.3vw;
}

.overlay:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

.fileInput {
  display: none;
}

.itemDetails {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 10px;
  margin-right: 10px;
  flex-grow: 1;
}

.itemInvoiceDetails {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 10px;
  margin-top: -15px;
  flex-grow: 1;
}

.itemName {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-style: normal;
  width: calc(100% - 15px); /* Adjust the width to prevent overflow */
  font-size: 5vw;
  font-weight: 500;
  margin-top: 0;
  margin: 0 5px;
  color: rgba(88, 55, 50, 1);
  background-color: transparent;
  text-transform: capitalize;
}

.itemInvoiceName {
  width: calc(260% - 15px);
  background-color: transparent;
  font-size: 1.3rem;
  font-weight: 500;
}

.multiplySymbol {
  font-weight: 600;
}

.qtyInvoice {
  font-weight: 500;
}

.itemPrice {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-style: normal;
  font-weight: 600;
  width: calc(100% - 15px); /* Adjust the width to prevent overflow */
  font-size: 3.3vw;
  /* margin-bottom: 35px; */
  margin-left: 5px;
  color: #3a3a3a;
  background-color: transparent;
}

.itemPriceInvoice {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 0.9rem;
  margin-left: 5px;
  color: #d9c61c;
  text-align: right;
  margin-top: 22px;
}

.itemQty {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  margin-left: 5px;
  color: #a8c7a9;
  fill: #a8c7a9;
  height: 40px;
}

.itemQtyValue {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-style: normal;
  font-weight: 600;
  margin-top: 19px;
  margin-left: 1px;
  margin-right: 1px;
  width: 25px;
  text-align: center;
}

.itemQtyInput {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-style: normal;
  font-weight: 600;
  width: 30px; /* Adjust the width to prevent overflow */
  font-size: 0.9rem;
  margin-bottom: 10px;
  text-align: center;
  background-color: transparent;
}

.addButton {
  background-color: #ffffff;
    border: 2px solid #a8c7a9;
    /* border: none; */
    display: inline-block;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    width: 87px;
    height: 32px;
    margin-left: 5px;
    margin-top: 5px;
    border-radius: 20px;
}
.grayscale {
  filter: grayscale(100%);
}

.disabled {
  color: gray;
}
.plusNegative {
  width: 35px;
  height: 35px;
  margin: 2.5px 0 -0.5px 0px;
}

.plusNegative2 {
  width: 84px;
  height: 21px;
  position: absolute;
  transform: rotate(45deg);
  left: -33px;
  top: 21px;
}
.remove {
  width: 25px;
  height: 25px;
  margin-top: -10px;
  margin-right: 10px;
}

.itemInvoice .itemDetails {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.itemInvoice .itemName,
.itemInvoice .itemPrice,
.itemInvoice .itemQty .qtyInvoice .multiplySymbol {
  font-size: 0.9rem;
}

.blank {
  border: 1px solid #000000;
}

.notblank {
  border: 1px solid #ffffff00;
}

.createItem {
  position: absolute;
  left: 15px;
  right: 15px;
}
